<template>
  <div>
     <h1>Button</h1>
     <h2>介绍</h2>
     <h2>引入</h2>
     <div  class="import-code"></div>
     <h2>Example</h2>
     <zbutton type="info">info</zbutton>
     <zbutton type="warning">warning</zbutton>
     <zbutton type="danger">danger</zbutton>
     <zbutton type="success">success</zbutton>
     <zbutton type="primary">primary</zbutton>
     
     <div class="code">
        <code><<span class='code-entity'>button</span> <span class='code-attr'>type</span>=<span class='code-tag'>info</span>>sd</<span class='code-entity'>button</span>></code>
        <code><<span class='code-entity'>button</span> <span class='code-attr'>type</span>=<span class='code-tag'>warning</span>>sd</<span class='code-entity'>button</span>></code>      
         <code> <<span class='code-entity'>button</span> <span class='code-attr'>type</span>=<span class='code-tag'>danger</span>>sd</<span class='code-entity'>button</span>> </code>     
         <code> <<span class='code-entity'>button</span> <span class='code-attr'>type</span>=<span class='code-tag'>success</span>>sd</<span class='code-entity'>button</span>> </code>     
         <code> <<span class='code-entity'>button</span> <span class='code-attr'>type</span>=<span class='code-tag'>primary</span>>sd</<span class='code-entity'>button</span>>  </code>                
     </div>
     
     <div class="use-code">Use</div>
     <zbutton  type="info-transparent">info</zbutton>
     <zbutton type="warning-transparent">warning</zbutton>
     <zbutton type="danger-transparent">danger</zbutton>
     <zbutton type="success-transparent">success</zbutton>
     <zbutton type="primary-transparent">primary</zbutton>
     <div class="code">
       <code><<span class='code-entity'>button</span> <span class='code-attr'>type</span>=<span class='code-tag'>info-transparent</span>>sd</<span class='code-entity'>button</span>></code>  
        <code><<span class='code-entity'>button</span> <span class='code-attr'>type</span>=<span class='code-tag'>warning-transparent</span>>sd</<span class='code-entity'>button</span>></code>      
        <code><<span class='code-entity'>button</span> <span class='code-attr'>type</span>=<span class='code-tag'>danger-transparent</span>>sd</<span class='code-entity'>button</span>> </code>     
        <code><<span class='code-entity'>button</span> <span class='code-attr'>type</span>=<span class='code-tag'>success-transparent</span>>sd</<span class='code-entity'>button</span>></code>      
        <code><<span class='code-entity'>button</span> <span class='code-attr'>type</span>=<span class='code-tag'>primary-transparent</span>>sd</<span class='code-entity'>button</span>></code>        
     </div>
     <div>loading</div>
     <zbutton type="info" icon-name="loading" :isLoading="true">loading</zbutton>
     <zbutton type="warning" icon-name="loading" :isLoading="true">loading</zbutton>
     <zbutton type="danger" icon-name="loading" :isLoading="true">loading</zbutton>
     <zbutton type="success" icon-name="loading" :isLoading="true">loading</zbutton>
     <zbutton type="primary" icon-name="loading" :isLoading="true">loading</zbutton>
     <div class="code">
       <code><<span class='code-entity'>button</span> <span class='code-attr'>type</span>=<span class='code-tag'>info</span>>sd</<span class='code-entity'>button</span>></code>  
        <code><<span class='code-entity'>button</span> <span class='code-attr'>type</span>=<span class='code-tag'>warning</span>>sd</<span class='code-entity'>button</span>></code>      
        <code><<span class='code-entity'>button</span> <span class='code-attr'>type</span>=<span class='code-tag'>danger</span>>sd</<span class='code-entity'>button</span>> </code>     
        <code><<span class='code-entity'>button</span> <span class='code-attr'>type</span>=<span class='code-tag'>success</span>>sd</<span class='code-entity'>button</span>></code>      
        <code><<span class='code-entity'>button</span> <span class='code-attr'>type</span>=<span class='code-tag'>primary</span>>sd</<span class='code-entity'>button</span>></code>        
     </div>
     <div>touming</div>
     <zbutton type="info-transparent" icon-name="loading" :isLoading="true">loading</zbutton>
     <zbutton type="warning-transparent" icon-name="loading" :isLoading="true">loading</zbutton>
     <zbutton type="danger-transparent" icon-name="loading" :isLoading="true">loading</zbutton>
     <zbutton type="success-transparent" icon-name="loading" :isLoading="true">loading</zbutton>
     <zbutton type="primary-transparent" icon-name="loading" :isLoading="true">loading</zbutton>
     <div class="code">
       <code><<span class='code-entity'>button</span>
              <span class='code-attr'>type</span>=<span class='code-tag'>info-transparent</span>
              <span class='code-attr'>icon-name</span>=<span class='code-tag'>loading</span>
              <span class='code-attr'>:isLoading</span>=<span class='code-entity'>true</span>>sd</<span class='code-entity'>button</span>> 
                     
        </code>  
        <code><<span class='code-entity'>button</span> <span class='code-attr'>type</span>=<span class='code-tag'>warning-transparent</span>>sd</<span class='code-entity'>button</span>></code>      
        <code><<span class='code-entity'>button</span> <span class='code-attr'>type</span>=<span class='code-tag'>danger-transparent</span>>sd</<span class='code-entity'>button</span>> </code>     
        <code><<span class='code-entity'>button</span> <span class='code-attr'>type</span>=<span class='code-tag'>success-transparent</span>>sd</<span class='code-entity'>button</span>></code>      
        <code><<span class='code-entity'>button</span> <span class='code-attr'>type</span>=<span class='code-tag'>primary-transparent</span>>sd</<span class='code-entity'>button</span>></code>        
     </div>
     <h2>Properties</h2>
     <h2>Properties</h2>
     <h2>Properties</h2>
     <h2>Properties</h2>
     <h2>Properties</h2>
     <h2>Properties</h2>
  </div>
</template>

<script>
import zbutton from '../components/zbutton'
export default {
  components:{
    zbutton
  }
}

</script>
<style scoped lang="scss">
  .code{
    margin: 13px 0;
    background: #1e1e1e;
    color: white;
    padding:10px 30px;
    display: flex;
    flex-direction: column;
    
    &>code{
      margin-top: 10px;
    }  
  }
</style>


